const initialState = [
  { url: '/overview', name: '' },
  { url: '/analytics', name: '' },
  { url: '/history', name: '' },
  { url: '/order', name: ' ' },
  { url: '/categories', name: '' },
];

export function links(state = initialState, action) {
  switch (action.type) {
    case 'LINKS_ACTIVATE':
      return action.links;
    default:
      return state;
  }
}
